import React, {Component, Fragment} from 'react';
import {Player, BigPlayButton } from 'video-react';

class PlayerProcessed extends Component {

    componentWillReceiveProps(newProps){
        if (!this.props.realtime.realtime){
            if(this.props.source !== newProps.source){
                this.source = newProps.source;
                this.processedVideoP.load();
            }
            if(this.props.playback !== newProps.playback){
                (!newProps.playback.isStopped)? (newProps.playback.isPlaying)?this.processedVideoP.play():this.processedVideoP.pause() :this.processedVideoP.load();
            }
        }
        
    }

    render(){
        var img = (
            <img style={{minHeight:360}}  id='processedVideo' src={this.props.realtime.outputsource} ref={el => this.processedVideoI = el} />
        );
        var video = (
            <Player ref={el => this.processedVideoP = el}>
                <source src={this.source}/>
                <BigPlayButton position="center" />
            </Player>
        );
        return(
            <Fragment>
                {this.props.realtime.realtime?'':video}
            </Fragment>
        );
    }
}


export default PlayerProcessed;