import axios from 'axios';
const hostname = 'http://localhost:5000/'

export const fileProcesssing = data => dispatch =>{
    startProcess()(dispatch);
    const url = hostname+'apiv1/startProcess/'+data.filename;
    axios
    .get(url)
    .then(res=>{
        console.log(res);
        recieveWS(res.data)(dispatch)
    })
}

export const startProcess = () => dispatch => {
    dispatch({
        type: 'startedprocess'
    });
}

export const recieveWS = (res) => dispatch =>{
        if(!res.error){
            dispatch({
                type: 'processDone',
                payload: res
            });
        }
        if(res.error){
            dispatch({
                type: 'errorProcessing',
                payload: res
            });
        }
}

