import axios from "axios";

export const togglePlayPause = (playback, realtime) =>  dispatch=>{
    dispatch({
        type: 'Play/Pause'
    });
    if(realtime.realtime){
        if(playback.isPlaying){
            axios
            .get('http://localhost:5000/ondevice/pause')
            .then(res=>{
                console.log(res.data.status)
            })
        }
        else{
            axios
            .get('http://localhost:5000/ondevice/play')
            .then(res=>{
                console.log(res.data.status)
                dispatch({
                    type:'setSources'
                })
            })
        }
    }
}

export const stop = realtime =>  dispatch =>{
    dispatch({
        type: 'Stop'
    });
    if(realtime.realtime){
        axios
        .get('http://localhost:5000/ondevice/stop')
        .then(res=>{
            console.log(res.data.status)
            dispatch({
                type:'resetRealTime'
            })
        })
    }
}

export const record = realtime =>  dispatch =>{
    dispatch({
        type: 'Record'
    })
}