import axios from 'axios';
import {fileProcesssing} from './Processing';

const hostname = 'http://localhost:5000/'

export const change = e =>  dispatch=>{
    console.log(e.target.files);
    dispatch({
        type: 'change',
        payload: e.target.files[0],
    })
}

export const fileupload = file =>  dispatch =>{
    const url = hostname+'apiv1/upload';
    const formData = new FormData();
    formData.append('file',file);
    const config = {
        headers: {
            'content-type': 'multipart/form-data'
        }
    }
    axios
        .post(url, formData, config)
        .then(res =>{    
            dispatch({
                type:   'fileupload',
                payload:{ 
                        file: res.data.filename, 
                        success:res.data.status, 
                    }
            });
            if(res.data.status) { 
                dispatch({
                    type: 'resetAll',
                });
                dispatch({
                    type: 'resetState'
                });
                fileProcesssing({filename:res.data.filename})(dispatch)
            }
        })
        .catch(err=>{
            console.log(err.message);
            dispatch({
                type: 'fileupload',
                payload: {form: true, success:false, message: err.message}
            });
        })
}


