import React, {Component, Fragment} from 'react';
// import io from 'socket.io-client';
import {Player, BigPlayButton } from 'video-react';

class Recorder extends Component {


    componentWillReceiveProps(newProps){
        if (!this.props.realtime.realtime){    
            if(this.props.source !== newProps.source){
                this.source = newProps.source;
                this.sourceVideoP.load()
            }
            if(this.props.playback !== newProps.playback){
                (!newProps.playback.isStopped)? (newProps.playback.isPlaying)?this.sourceVideoP.play():this.sourceVideoP.pause() :this.sourceVideoP.load();
            }
        }
        
    }
    
    render(){
        var img = (
            <img style={{minHeight:360}}  id='sourceVideo' src={this.props.realtime.inputsource} ref={el => this.sourceVideoI = el} />
        )
        var video = (
            <Player ref={el => this.sourceVideoP = el}>
                <source src={this.source}/>
                <BigPlayButton position="center" />
            </Player>
        )
        return(
            <Fragment>
                {this.props.realtime.realtime?'':video}
            </Fragment>
        )
    }
    
}




export default Recorder;
  