import {togglePlayPause} from './Playback';

export const realTime = () => dispatch =>{
    togglePlayPause({isPlaying:false }, {realtime:true})(dispatch);
    dispatch({
        type: 'realTime',
        payload: 'Playing'
    })
    dispatch({
        type: 'resetFormAll',
    })
    
}

export const startRealTime = () => dispatch =>{
    dispatch({
        type: 'startRealTime',
    })
}
