import React, {Component, Fragment} from 'react'
import {Col, Row} from 'reactstrap';
import {connect} from 'react-redux';
import Info from './Info';
import PlayerProcessed from './PlayerProcessed';
import Recorder from './Recorder';


class Video extends Component{

    componentWillReceiveProps(newProps){
        if (this.props.process !== newProps.process){
            this.dataVideo = 'http://localhost:5000' + newProps.process.dataVideo;
            this.originalVideo = 'http://localhost:5000' + newProps.process.originalVideo;
            this.processedVideo = 'http://localhost:5000' + newProps.process.processedVideo;
        }
    }

    render(){
        return(
            <Fragment >
                <Row>
                    <Col style={{marginTop:10}} className='text-center' >
                        <Info realtime={this.props.realtime} playback={this.props.playback} source={this.dataVideo}/>
                    </Col>
                </Row>
                <Row>
                    <Col style={{marginTop:10}}>
                        <Recorder realtime={this.props.realtime} playback={this.props.playback} source={this.originalVideo}/>
                    </Col>
                    <Col style={{marginTop:10}}>
                        <PlayerProcessed realtime={this.props.realtime} playback={this.props.playback} source={this.processedVideo}/>
                    </Col>
                </Row>
            </Fragment>
        )
    }
}


const mapStateToProps = state => ({
    file: state.File,
    playback: state.Playback,
    process: state.Processing,
    realtime: state.RealTime
});
export default connect(mapStateToProps)(Video);