import React, {Component} from 'react';
import {connect} from 'react-redux';
import {change, fileupload} from '../../Actions/File.js';
import Loading from '../../Assets/loading.gif';

class FileUpload extends Component {
    constructor(props) {
        super(props);
        this.onFormSubmit = this.onFormSubmit.bind(this);
        this.loading = (
            <img style={{width:45}} src={Loading} />
            );
        this.state={
            loading: false
        }
    }

    onFormSubmit(e){
        e.preventDefault() // Stop form submit
        this.setState(() => ({
            loading: true
        }))
        if(this.props.file.file){
            this.props.fileupload(this.props.file.file)
        }
    }
    componentWillReceiveProps(){
        if (this.props.file.message){
            this.setState(() => ({
                loading: false
            }))
        }
    }
    render() {
        return (
            <div id='form'>
                <form >
                    <div className="form-group">
                        {this.props.file.message?this.props.file.message:this.state.loading?this.loading:''}
                    </div>
                    <div className="form-group">
                        <input id='fileUpload' type="file" onChange={this.props.change}/>
                    </div>
                    <button onClick={this.onFormSubmit} className="btn btn-success" type='submit'>Upload</button>
                </form>
            </div>
      )
    }
}

const mapStateToProps = state => ({
    file: state.File
});

export default connect(
    mapStateToProps,
    { change, fileupload }
)(FileUpload);