
const initialState = {
    isPlaying: false, 
    isRecording: false, 
    isStopped: true
};

export default (state = initialState, action)=>{
    switch(action.type){
        case 'Play/Pause':
            return{
                ...state,
                isPlaying: !state.isPlaying,
                isStopped:false
            }
        case 'Stop':
            return{
                ...state,
                isPlaying: false, 
                isRecording: false, 
                isStopped: true
            }
        case 'Record':
            return{
                ...state,
                isRecording: true,
                isStopped: false,
                isPlaying: true
            }
        default:
            return state;
    }
}