import React, {Component, Fragment} from 'react';
import {Dropdown, DropdownToggle, DropdownMenu, DropdownItem} from 'reactstrap';
import {connect} from 'react-redux';
import {renderUploadForm} from '../../Actions/Forms';
import {realTime} from '../../Actions/RealTime';

class Options extends Component{

    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.handleClickUpload = this.handleClickUpload.bind(this);
        this.handleRealTime = this.handleRealTime.bind(this);
        this.state = {
          dropdownOpen: false,
        };
      }
    
      toggle() {
        this.setState(prevState => ({
          dropdownOpen: !prevState.dropdownOpen
        }));
      }
      handleClickUpload(){
      this.props.renderUploadForm()
      }
      handleRealTime(){
        this.props.realTime()
      }
    render(){
        return(
          <Fragment>
            <Dropdown  id='ddtoggle' style={{margin:10, width:200}} isOpen={this.state.dropdownOpen} toggle={this.toggle}>
                <DropdownToggle style={{ width:200}} caret>
                    Options
                </DropdownToggle>
                <DropdownMenu>
                <DropdownItem onClick={this.handleRealTime}>Real Time SelfCam</DropdownItem>
                <DropdownItem onClick={this.handleClickUpload} id="fileUploadButton">File Upload</DropdownItem>
                <DropdownItem divider />
                <DropdownItem disabled>Real Time</DropdownItem>
                </DropdownMenu>
            </Dropdown>
          </Fragment>
        )
    }
}

export default connect(
    null,
    { renderUploadForm, realTime}
)(Options);