import React, {Component, Fragment} from 'react';
import FileUpload from './Upload';
import {connect} from 'react-redux';

class Forms extends Component{

    constructor(props){
        super(props)
        this.form = null
    }
    
    componentWillReceiveProps(newProps){
        if (newProps.forms.uploadform){
            this.form = (
                <FileUpload />
            )
        }
    }

    componentDidUpdate(){
        if (this.props.forms.uploadform){
            this.form = (
                <FileUpload />
            )
        }
    }
    

    render(){
        
        return(
            <Fragment>
                {this.props.forms.display? this.form: ''}
            </Fragment>
            
        )
    }
}

const mapStateToProps = state => ({
    file: state.File,
    forms: state.Forms
});

export default connect(
    mapStateToProps,
)(Forms);