import Loading from '../Assets/loading.gif';

const initialState = {
    message: null,
    status: null,
    originalVideo: null,
    processedVideo: null,
    dataVideo: null
}

export default (state = initialState, action)=>{
    switch(action.type){
        case 'resetState':
            return{
                message: null,
                status: null,
                originalVideo: null,
                processedVideo: null,
                dataVideo: null
            }
        case 'startedprocess':
            return{
                ...state,
                message: "Processing",
                icon: Loading,
                status: 0,
            }
        case 'processDone':
            return{
                ...state,
                message: action.payload.message,
                status: action.payload.status,
                originalVideo: action.payload.original_file_url,
                processedVideo: action.payload.output_video_url,
                dataVideo: action.payload.data_url
            }
        case 'errorProcessing':
            return{
                ...state,
                message: action.payload.message,
                status: 0
            }
        default:
            return state;
    }
}