const initialState = {
    form: false,
    file: null,
    success: false,
    message: null
}

export default (state = initialState, action)=>{
    switch(action.type){
        case 'change':
            return{
                ...state,
                file: action.payload
            }
        case 'fileupload':
            return{
                ...state,
                success: action.payload.success,
                file: action.payload.file,
                message: action.payload.message
            }
        default:
            return state;
    }
}