import React, {Component, Fragment} from 'react';
import {Player, BigPlayButton } from 'video-react';

class Info extends Component {

    componentWillReceiveProps(newProps){
        if (!this.props.realtime.realtime){
            if(this.props.source !== newProps.source){
                console.log(this.props.source + newProps.source)
                this.source = newProps.source;
                this.videoDataV.load()
            }
            if(this.props.playback !== newProps.playback){
                (!newProps.playback.isStopped)? (newProps.playback.isPlaying)?this.videoDataV.play():this.videoDataV.pause() :this.videoDataV.load();
            }
        }
    }
    render(){   
        var img = (
                <img style={{minHeight:312}} id='videoData' src={this.props.realtime.datasource} ref={el => this.videoDataI = el} />
        );
        var video = (
                <Player  ref={el => this.videoDataV = el} >
                    <source src={this.source}></source>
                    <BigPlayButton position="center" />
                </Player>
        )
        return(
            <Fragment>
                {this.props.realtime.realtime?img:video}
            </Fragment>

        );
    }
}

export default Info;