import React, {Component, Fragment} from 'react';
import {Row, Col} from 'reactstrap';
import Play from '../../Assets/Play.ico';
import Pause from '../../Assets/Pause.ico';
import Stop from '../../Assets/Stop.ico';
import Record from '../../Assets/Record.ico';
import { connect } from "react-redux";
import {togglePlayPause, stop, record} from '../../Actions/Playback';

class PlaybackB extends Component{

    render(){
        var img = (
            <Row>
                <Col md='2'/>
                <Col md='1'><button onClick={e => this.props.stop(this.props.realtime)} className='btn'><img  className='playback-btn' src={Stop} alt='Stop Rec'/></button></Col>
                <Col md='1'><button onClick={e=>this.props.record( this.props.realtime)} className='btn'><img  className='playback-btn' src={Record} alt={(this.props.status.isRecording)? 'Pause' : 'Record'}/></button></Col>
                <Col md='4'className='text-center'>{(this.props.status.isRecording)? 'Recording' : ''}</Col>
                <Col md='1'><button onClick={e=>this.props.togglePlayPause(this.props.status, this.props.realtime)} className='btn'><img  className='playback-btn' src={(this.props.status.isPlaying)? Pause : Play} alt={(this.props.status.isPlaying)? 'Pause' : 'Play'}/></button></Col>
                <Col md='1'><button onClick={e=>this.props.stop(this.props.realtime)} className='btn'><img  className='playback-btn' src={Stop} alt='Stop'/></button></Col>
                <Col md='2'/>
            </Row>
    )
        
        var video = (
                <Row>
                    <Col md='2'/>
                    <Col md='1'><button onClick={e => this.props.stop(e)} className='btn'><img  className='playback-btn' src={Stop} alt='Stop Rec'/></button></Col>
                    <Col md='1'><button onClick={e => this.props.record(e)} className='btn'><img  className='playback-btn' src={Record} alt={(this.props.status.isRecording)? 'Pause' : 'Record'}/></button></Col>
                    <Col md='4'className='text-center'>{(this.props.status.isRecording)? 'Recording' : ''}</Col>
                    <Col md='1'><button onClick={e => this.props.togglePlayPause(e)} className='btn'><img  className='playback-btn' src={(this.props.status.isPlaying)? Pause : Play} alt={(this.props.status.isPlaying)? 'Pause' : 'Play'}/></button></Col>
                    <Col md='1'><button onClick={e => this.props.stop(e)} className='btn'><img  className='playback-btn' src={Stop} alt='Stop'/></button></Col>
                    <Col md='2'/>
                </Row>
        )
        return(
            <Fragment>
                {this.props.realtime.realtime?img:img}
            </Fragment>
        )
    }
}

const mapStateToProps = state => ({
    status: state.Playback,
    realtime:state.RealTime
});

export default connect(
    mapStateToProps,
    { togglePlayPause, record, stop }
)(PlaybackB);
  