import Loading from '../Assets/loading.gif';

const initialState = {
    display: false,
    message: null,
    icon: null
}

export default (state=initialState, action)=>{
    switch(action.type){
        case 'processing':
            return{
                ...state,
                display:true,
                message: 'Processing Started',
                icon: Loading
            }
        case 'processingDone':
            return{
                ...state,
                display:true,
                message: 'Processing Finished',
                icon: null
            }
        default:
            return state
    }
}