const initialState = {
    realtime: false,
    source: null
}

export default (state=initialState, action)=>{
    switch(action.type){
        case 'realTime':
            return{
                ...state,
                realtime: true,
                message:action.payload.message,
                
            }
        case 'setSources':
            return{
                ...state,
                datasource: 'http://localhost:5000/ondevice/view_final_img',
                inputsource: 'http://localhost:5000/ondevice/input_video_viewer',
                outputsource: 'http://localhost:5000/ondevice/output_video_viewer'
            }
        case 'resetRealTime':
            return initialState
        default:
            return state
    }
}