const initialState = {
    display: false,
    uploadform: false
}

export default (state = initialState, action)=>{
    switch(action.type){
        case 'uploadform':
            return{
                ...state,
                display: action.payload.display,
                uploadform: action.payload.uploadform
            }
        case 'resetFormAll':
            return {
                ...initialState
            }
        default:
            return state;
    }
}